import { createMuiTheme } from "@material-ui/core/styles";

export default createMuiTheme({
  palette: {
    primary: {
      light: "#484848",
      main: "#212121",
      dark: "#000000",
      contrastText: "#fff"
    },
    secondary: {
      light: "#ff6549",
      main: "#e62b1e",
      dark: "#ab0000",
      contrastText: "#fff"
    }
  }
});



